@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&family=Roboto:wght@500&display=swap');

*{
  padding: 0;
  margin: 0;
}

body{
  background-color: #000;
  overflow-x: hidden;
}
.App{
  width: 540px;
  height: 100vh;
  margin: auto;
  display: block;
  position: relative;
}
.header{
  width: 100%;
  height: auto;
  border-bottom: .5px solid #333;
  padding: 10px;
}
.header > h3{
  text-align: center;
  color: #ccd3ce;
  font-family: 'Inter', sans-serif;
  font-weight: 700;
  font-size: 40px;
}
.wordle-game{
  width: 100%;
  height: auto;
  box-sizing: border-box;
  margin: 20px 0px 0px 0px;
}
.wordle-word{
  width: 100%;
  box-sizing: border-box;
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  margin-left: 10px;

}
.wordle-word > div{
  border: 2px solid #3a3a3c;
  margin: 3px;
  color: #ccd3ce;
  height: 52px;
  flex: 0 1 calc(52px );
  text-align: center;
  background-color: transparent;
  font-size: 45px;
  padding: 0px 5px 5px 5px;
  font-family: 'Inter', sans-serif;
  font-weight: 700;
  text-transform: uppercase;
  border-radius: 5px;
}
.wordle-keyboard{
  width: 100%;
  height: auto;
  bottom: 0px;
  position: absolute;
  margin: 10px;
}
.wordle-keyboard > .letter{
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  margin: 10px;
}

.wordle-keyboard > .letter > div{
  padding: 17px;
  background-color: #3a3a3e;
  font-family: 'Inter', sans-serif;
  font-weight: bold;
  margin: 3px;
  text-transform: uppercase;
  color: #ffffffb0;
  border-radius: 5px;
  cursor: pointer;
}
.wordle-keyboard > .letter > div:hover{
  background-color: #48484a !important;
}
.wordle-keyboard > .letter:nth-child(3) > div{
  padding-bottom: 8px !important
}
.wordle-keyboard > .letter:nth-child(3) > div > svg{
  position: relative;
  top: -2px;
}
.yellow{
  background-color: #b1a03b !important;
  border-color: #b1a03b !important;
}

.green{
  background-color: #548e4f !important;
  border-color: #548e4f !important; 
  
}

.grey{
  background-color: #3a3a3e !important;
  border-color: #3a3a3e !important;
}
.transparent{
  background-color: transparent !important;
  border-color: #3a3a3c !important;
}
.has{
  background-color: #606060 !important;
  border-color: #606060 !important;
}